/* Contenidor principal per a Shipping Info i Cart Summary */
.shippingInfoContainer {
  display: flex;
  justify-content: space-between;
  width: 60%; /* Ajusta l'ample segons el que necessitis */
  margin: 0 auto;
  padding-top: 80px;
  padding-bottom: 80px;
  gap: 20px;
}

/* Estil per a la secció d'informació d'enviament */
.shippingInfoSection {
  width: 40%;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Alineació del títol i botó de sign-in */

.shippingHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.userAddressSection {
  margin-top: 35px;
  margin-bottom: 35px;
  font-size: 14px;
}

.userAddressSection p {
  line-height: 1; /* Ajusta aquest valor segons sigui necessari */
   /* Si vols controlar també l'espai entre paràgrafs */
}

.signInLink {
  background: none;
  color: #1e1f1f;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  font-size: 16px;
}

.shippingMethod {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  padding-bottom: 30px;
  padding-top: 30px;
}

.radioButton {
  flex-shrink: 0; /* Manté el botó d'opció en la seva mida natural */
}

.shippingLabel {
  flex-grow: 1; /* Permet que el text s'ajusti a l'espai disponible */
  margin-left: 10px; /* Espai entre el botó i el text */
  white-space: nowrap; /* Evita que el text es trenqui en línies noves */
}

.shippingPrice {
  flex-shrink: 0; /* Manté el preu alineat a la dreta */
  margin-left: 20px; /* Espai entre el text i el preu */
}

/* Estil per als elements de la secció de la cistella */
.cartSummary {
  width: 35%;
  background-color: #fff;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 40px;
  padding-left: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Detalls i informació extra */
.additionalInfo {
 font-size: 12px;
  width: 74%;
  margin-top: 10px;
  text-align: justify;
  background-color: #f7f7f7;
  padding-right: 60px;
  padding-left: 60px;
  padding-top: 25px;
  padding-bottom: 30px;
}

.additionalInfo p{
    margin-top: -10px;

}

h4 {
  color: #e63946; /* Vermell pels títols destacats */
}

.paymentButton {
  width: 100%;
  background-color: #1e1f1f;
  color: #d7b46a;
  padding: 15px 30px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 40px;
  margin-bottom: 15px;
  border: none;
}

.paymentButton:hover {
  background-color: #d7b46a;
  color: #1e1f1f;
}

/* Alineació de la informació de seguretat */
.secureInfo {
  display: flex;
  align-items: center;
  margin-top: 0px;
}

.secureInfo img {
  width: 20px;
  margin-right: 10px;
}

/* Estils generals */
h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.signInLink {
  background: none;
  background-color: rgba(255, 255, 255, 0.473); /* 0.8 representa el 80% d'opacitat */

  color: #1e1f1f;
  border-color: #d7b46a;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  font-size: 1em;
  margin-bottom: 0px;
}

.signInLink:hover {
  color: #1e1f1f;
}

/* Divisor entre seccions */
.divider {
  margin-top: 15px;
  border-top: 2px solid #ccc;
}

.quantityControls {
  width: 250px;
}

.cartActions {
  width: 380px;
  gap: 5px;
}
