/* Contenidor general per a les dues seccions */
.checkoutContainer {
  display: flex;
  justify-content: space-between;
  width: 60%; /* Assegura que té el mateix ample que shippingInfoContainer */
  margin: 0 auto;
  padding-top: 80px;
  padding-bottom: 80px;
  gap: 20px;
}

/* Ajusta l'amplada del contenidor i l'alineació dels elements */
.promoCheckbox {
  display: flex;
  align-items: center;
  margin-bottom: 35px;
}

.promoCheckbox input[type="checkbox"] {
  margin-right: -210px; /* Espai entre el checkbox i el text */
  margin-left: -210px;
  transform: scale(1.2); /* Ajusta la mida si és massa petita */
}

.promoCheckbox label {
  font-size: 16px; /* Ajusta el tamany de la font si cal */
}

/* Contenidors per als botons de pagament */
.paymentOptions {
  display: flex;
  justify-content: space-between; /* Afegeix espai entre els botons */
  gap: 20px; /* Això afegeix l'espai definit entre botons */
  width: 50%; /* Assegura que ocupen el 100% de l'ample disponible */
  margin-top: -20px;
  margin-bottom: 40px;
}

.paypalButton,
.amazonButton {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 180px;
  height: 45px;
  background-color: #d7b46a;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s, box-shadow 0.2s;
}

.paypalButton img,
.amazonButton img {
  width: 60%; /* Ajusta la mida de les icones */
  height: auto;
  display:block;
}

.amazonButton img {
  margin-top: 8px; /* Assegura que la imatge es centri verticalment */
}

.paypalButton:hover,
.amazonButton:hover {
  transform: translateY(-3px); /* Efecte de moviment cap a dalt */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Intensifica l'ombra en hover */
}

/* Checkout i Cart Summary */
.checkoutForm {
  width: 40%;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Contenidor de la secció de la cistella */
.cartSummary {
  width: 35%;
  background-color: #fff;
  padding: 20px 40px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Camps del formulari */
.formInput {
  margin-bottom: 10px;
  padding-right: 200px;
  padding-bottom: 15px;
  border: none;
  box-shadow: 0 0 0 0.3px #1b1b1b;
  width: 82%;
  font-size: 14px;
}
.formRow {
  display: flex;
  gap: 0px;
}

.formRow .formInput {
  width: 90%; /* Ajustem l'amplada per ocupar la meitat de la fila */
}
.formRowLarge {
  width: 115%;
}

/* Estructura del producte */
.productGrid {
  display: flex;
  gap: 20px;
}

/* Estil de la imatge del producte */
.productImage {
  width: 120px;
  border-radius: 8px;
  object-fit: cover;
}

/* Detalls del producte */
.productDetails {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Preu del producte */
.productPrice {
  font-size: 18px;
  color: #333;
  font-weight: bold;
}

/* Resum de la comanda */
.summaryRow {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.summarySection {
  padding-top: 20px;
}

/* Divisor entre seccions */
.divider {
  margin-top: 15px;
  border-top: 2px solid #ccc;
}

/* Estil del total estimat */
.totalRow {
  display: flex;
  justify-content: space-between; /* Alinea els elements a banda i banda */
  font-weight: bold;
  font-size: 18px;
  color: #000;
}

/* Estil per a "Currency notice" */
.currencyNotice {
  margin-top: 100px;
  font-size: 14px;
  color: #999;
  text-align: justify;
  font-size: 12px;
}

.noticeHighlight {
  color: #e63946;
  font-weight: bold;
}

button[type="submit"] {
  background-color: #1e1f1f; /* Color de fons */
  color: #d7b46a; /* Color del text */
  border: none;
  padding: 15px 30px; /* Mida del botó */
  font-size: 14px; /* Tamany del text */
  width: 40%;
  cursor: pointer; /* Canvia el cursor quan passi per sobre */
  border-radius: 5px; /* Cantonades arrodonides */
  transition: background-color 0.3s ease; /* Transició per canviar color */
  margin-top: 20px;
}

button[type="submit"]:hover {
  background-color: #d7b46a; /* Color de fons quan el cursor estigui sobre */
  color: #1e1f1f;
}

.secureInfo img {
  width: 20px; /* Defineix l'amplada de la icona */
  height: auto; /* Manté la proporció original de la imatge */
  margin-right: 8px; /* Afegeix espai entre la icona i el text */
}

/* Contenidor per a l'increment i decrement de quantitat */
.quantityControls {
  display: flex;
  align-items: left;
  gap: 10px; /* Separació entre botons i text */
  margin-top: 10px;
  width: 66%;
}

.quantityControls button {
  background-color: #e0e0e0;
  color: #1e1f1f;
  border: none;
  padding: 5px 5px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.quantityControls button:hover {
  background-color: #d7b46a;
}

.quantityControls span {
  font-size: 16px;
  font-weight: bold;
}

/* Estil per als enllaços d'accions del producte */
.cartActions {
  display: flex;
  margin-top: -18px;
}

.cartActions button {
  background: none;
  border: none;
  color: #1e1f1f;
  cursor: pointer;
  text-decoration: underline;
  font-size: 14px;
  transition: color 0.3s ease;
  width: 33%;
}

.cartActions button:hover {
  color: #0056b3;
}

/* Estil per a la icona de tancar (si l'afegim) */
.productImage {
  position: relative;
  width: 130px;
  height: 200px;
}

.productImage::after {
  content: url("../assets/icons/cerrar.png");
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
